.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
}

.navbar-list {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

.navbar-item {
  margin: 0 10px;
}

.about-icon {
  height: 50px;
  width: 50px;
}

.project-icon {
  height: 50px;
  width: 50px;
}

.skill-icon {
  height: 50px;
  width: 50px;
}

.curriculum-icon {
  height: 50px;
  width: 50px;
}

@media screen and (max-width: 600px) {
  .navbar {
    height: auto;
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }

  .navbar-item {
    margin: 10px 0;
  }
}
